import { Button, Divider } from 'antd';
import React, { useEffect, useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { API_KEY, API_URL, IMAGE_BASE_URL } from '../Config';
import MainImage from '../LandingPage/Section/MainImage';
import ImageList from './ImageList';
import MovieInfo from './MovieInfo';




const Detail = () => {
  const { movieId } = useParams()
  const navigate = useNavigate();

  // [state]
  const [Movie, setMovie] = useState({});
  const [Casts, setCasts] = useState([]);
  const [Crew, setCrew] = useState([]);
  const [ActorToggle, setActorToggle] = useState(false);
  const [CrewToggle, setCrewToggle] = useState(false);

  useEffect(() => {

    // [  ] URL
    let endpointinfo = `${API_URL}${movieId}?api_key=${API_KEY}`
    // console.log(endpointinfo)

    let endpointCrew = `${API_URL}${movieId}/credits?api_key=${API_KEY}`
    // console.log(endpointCrew)

    // [  ]    
    fetch(endpointinfo)
      .then(response => response.json())
      .then(response => {
        // console.log(response)
        setMovie(response);
      })

    // []  ,   
    fetch(endpointCrew)
      .then(response => response.json())
      .then(response => {
        console.log(response.crew);
        setCasts(response.cast);
        return response.crew;
      })
      .then((response) => {  //    
        const idSave = new Set();
        const searchCrew = response.filter((crew) => {
          const isIn = idSave.has(crew.id);
          idSave.add(crew.id);
          return !isIn;
        })
        setCrew(searchCrew);
      })
  }, []);

  ////  
  function toggleActorView() {
    // console.log('')
    setActorToggle(!ActorToggle)
    // console.log(ActorToggle)
  }

  function toggleCrewView() {
    setCrewToggle(!CrewToggle)
  }

  return (
    <>
      <div>
        <a href='/items'>items  </a>
      </div>
      {/* Header */}
      {Movie && <MainImage image={`${IMAGE_BASE_URL}w1280${Movie.backdrop_path}`}
        title={Movie.title}
        overview={Movie.overview}
        tagline={Movie.tagline} />}

      {/*    */}
      <div style={{ textAlign: 'center', marginTop: '60px' }}>
        <Button onClick={() => navigate(-1)} >   </Button>
      </div>

      {/* Body */}
      <div style={{
        width: '85%',
        margin: '30px auto'
      }}>

        {/* Movie Info */}
        <MovieInfo movie={Movie} />
        <br />
        {/* Actors Grid */}
        <div style={{
          textAlign: 'center',
          margin: '30px',
        }}>
          <Button onClick={toggleActorView} style={{ margin: '0 30px' }} type={ActorToggle ? 'primary' : 'dashed'}>   </Button>
          <Button onClick={toggleCrewView} style={{ margin: '0 30px' }} type={CrewToggle ? 'primary' : 'dashed'}>   </Button>
        </div>
        {ActorToggle && <div>
          <Divider style={{ border: '#0ff ' }}> </Divider>
          <ImageList targets={Casts} />  </div>
        }
        {CrewToggle && <div>
          <Divider style={{ border: '#777' }}> </Divider>
          <ImageList targets={Crew} />
        </ div>}


      </div>

    </>
  )
}

export default Detail